export enum JumpToCalendarEventType {
  TITLE = 'title',
  ICON = 'icon',
  RECEIVER = 'receiver',
}

export enum CalendarType {
  SHANHUI = 'shanhui',
}

export type ZhiCalendarCardPluginConfig = {
  locale?: {
    refresh: string;
    refreshTips: string; // 日程卡片有更新
    receiverCount: string; // 等{count}人参会
    viewCalendardetails: string; // 查看日程详情
    noresponse: string; // 未响应
    received: string; // 已接收
    refused: string; // 已拒绝
    pending: string; // 待定
    organizer: string; // 组织人
  };
  isMobile?: boolean;
  jumpToCalendar?: (link?: string, eventType?: JumpToCalendarEventType) => void;
  openProfile?: (uid: number) => void;
  jumpToAttachment?: (url: string) => void;
  mode?: CalendarType;
};
